html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Lexend", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 2rem 0;
}

/** Media Queries **/
h1,
h2,
h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
}

.barra {
  display: none;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra__nombre {
  font-size: 6rem;
  font-family: "League Gothic", sans-serif;
  color: bisque;
}
@media (min-width: 768px) {
  .barra__navegacion {
    font-size: 1.8rem;
    display: flex;
    gap: 4rem;
  }
}
.barra__link {
  color: #FFFFFF;
  font-size: 3rem;
  font-family: "League Gothic", sans-serif;
}
.barra__nombre {
  font-size: 8rem;
  color: #FFFFFF;
  margin: 0;
}

.formulario {
  margin: 5rem auto 2rem auto;
  background-color: #FFFFFF;
  border-radius: 5rem;
  box-sizing: 0px 3px 7px 0px #000000;
  padding: 3rem 0;
}
@media (min-width: 1024px) {
  .formulario {
    display: flex;
    justify-content: space-between;
    max-width: 90rem;
    padding: 0;
  }
}
.formulario__campo {
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .formulario__campo:nth-child(1) {
    padding-left: 4rem;
  }
}
.formulario__campo:hover {
  background-color: #dad5d5;
  border-radius: 5rem;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.4);
}
.formulario__label {
  font-size: 1.2rem;
  font-weight: 900;
}
.formulario__input {
  border: none;
  width: 100%;
  color: #686868;
}
.formulario__submit {
  display: block;
  height: 4rem;
  border: none;
  border-radius: 2rem;
  background-image: radial-gradient(circle, #82c1da 0%, #2c7997 100%);
  padding: 0 2rem;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.2rem;
}

.header {
  background-image: url(../img/cascada1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  padding: 3rem 0 20rem 0;
}
.header__busqueda {
  margin-top: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header__busqueda {
    margin-top: 30rem;
  }
}
.header__busqueda-boton {
  background-color: #FFFFFF;
  padding: 2rem 4rem;
  color: #5aaecf;
  margin-top: 1rem;
  border-radius: 3rem;
  font-size: 2rem;
  font-weight: 700;
  display: block;
}
@media (min-width: 768px) {
  .header__busqueda-boton {
    display: inline-block;
  }
}